.section-5 {
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 3;
  height: 14vh;
  position: absolute;

  .section-wrapper {
    display: grid;
    grid-template-rows: 14vh;
    justify-content: flex-end;
    grid-template-columns: repeat(5,1fr) 16vw;
  }

  .booking-button {
    display: flex;
    cursor: pointer;
    color: var(--white);
    align-items: center;
    justify-content: center;
    background-color: var(--blue);
    transition: all .3s ease-in-out;
    &:hover {
      background-color: darken(#0F4EAA,5);
    }

    .button-text {
      font-weight: 500;
      margin-right: .75rem
    }

  }

  .trial-tab {
    display: flex;
    cursor: pointer;
    padding: 0 1.5rem;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    transition: all .3s ease-in-out;

    &:hover {
      background-color: var(--blue);
    }

    .trial-icon {
      height: 1.25rem;
      margin-bottom: .5rem;
    }
    .trial-caption {
      font-size: .7rem;
      color: var(--white);
    }
  }

  .booking-tab {
    display: grid;
    gap: 0 1.25rem;
    cursor: pointer;
    padding: 0 1.5rem;
    position: relative;
    align-items: center;
    color: var(--text-blue);
    justify-content: center;
    background-color: #EBEBEB;
    grid-template-columns: 2rem 1fr;
    transition: all .3s ease-in-out;

    &:hover {
      color: var(--white) !important;
      background-color: darken(#0F4EAA,5);
    }

    &::before {
      top: 0;
      left: 0;
      height: 100%;
      width: 2px;
      content: '';
      display: block;
      position: absolute;
      background: radial-gradient(4175% 59.22% at 75% 50%, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0) 100%);
    }

    &:nth-child(2) {
      &::before{ display: none }
    }

    .tab-icon {
      width: 2rem;
      height: 2rem;
    }

    .tab-title {
      font-size: 1rem;
      grid-column: 2/-1;
      font-weight: 700;
      font-family: var(--bold);
      text-transform: uppercase;
      letter-spacing: 0.02rem;
    }

    .tab-caption {
      font-size: .65rem;
      font-weight: 600;
      grid-column: 2/-1;
      margin-top: 0.1rem;
    }

    .tab-footer {
      font-size: .65rem;
      font-weight: 600;
      grid-column: 2/-1;
      margin-top: 0.25rem;
    }

  }

}

@media all and (min-device-width: 320px) and (max-device-width: 720px) {

  .section-5 {
    z-index: 3;
    height: auto;
    position: relative;

    .section-wrapper {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 17vh repeat(4,15vh) 12vh;
    }

    .trial-tab {
      padding: 0 1.25rem;
      background-color: var(--orange);
    }

    .booking-tab {
      padding: 0 1.25rem;

      &::before {
        top: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background: radial-gradient(4175% 59.22% at 75% 50%, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0) 100%);
      }

    }

  }

}
