.section-1{display:grid;grid-gap:1.2rem;color:var(--white);padding:2rem 2.5rem;position:relative;background-color:var(--dark);grid-template-columns:1rem 1fr;grid-template-rows:2.25rem 18rem auto}.section-1 .heading{display:flex;grid-column:1/-1;align-items:center;justify-content:space-between}.section-1 .heading .logo{cursor:pointer}.section-1 .heading .languages{display:flex;align-items:center}.section-1 .heading .languages .language{cursor:pointer;margin-left:1rem}.section-1 .scroll-img-component{cursor:pointer;height:18rem;z-index:2}.section-1 .plane-img-component{z-index:2;top:-6.5rem;right:-2.5rem;position:absolute;transform:scale(0.8);pointer-events:none}.section-1 .world-map{left:0;bottom:0;z-index:1;height:18rem;position:absolute;pointer-events:none}.section-1 .main-component{z-index:2;height:18rem;position:relative;padding-top:.5rem;padding-left:.75rem}.section-1 .app-title{width:20rem;height:7.7rem;pointer-events:none;margin-bottom:1.5rem}.section-1 .app-caption{font-weight:400;line-height:1.5;font-size:.7rem;max-width:20rem;margin-bottom:1.5rem;letter-spacing:0.01rem}.section-1 .call-to-action{display:flex;cursor:pointer;align-items:center;padding:.65rem 2rem;border-radius:0.1rem;letter-spacing:0.01rem;background-color:var(--blue);transition:all .3s ease-in-out}.section-1 .call-to-action:hover{background-color:#0d4393}.section-1 .call-to-action .cta-text{font-weight:400;font-size:0.75rem;margin-right:.75rem}.section-1 .call-to-action .cta-icon{width:1.25rem;height:1.25rem}@media all and (min-device-width: 320px) and (max-device-width: 720px){.section-1{width:100vw;grid-gap:0rem;padding:1rem 0;grid-template-columns:1fr;grid-template-rows:2.25rem auto}.section-1 .scroll-img-component{display:none}.section-1 .heading{padding:0 1.25rem}.section-1 .main-component{z-index:2;height:auto;overflow:hidden;padding:2rem 1.25rem}.section-1 .plane-img-component{z-index:1;top:-2rem;width:100%;right:-7rem;opacity:0.55;transform:scale(0.8)}.section-1 .world-map{width:100%