.section-2{position:relative;background-size:cover;background-position:center;background-repeat:no-repeat;background-image:url(/Travlo/static/media/section-2.844fa5a8.png)}.section-2 .section-wrapper{padding:2rem;display:flex;height:100%;flex-direction:column;justify-content:space-between;background:linear-gradient(229.33deg, rgba(14,20,20,0.1) -41.78%, rgba(14,20,20,0.2) 100.54%)}.section-2 .section-heading{display:grid;grid-gap:.75rem;align-items:center;grid-template-columns:10.5rem 1fr 1.5rem}.section-2 .section-heading .title{font-weight:500;font-size:.85rem;color:var(--white)}.section-2 .section-heading .divider{height:1px;background-color:var(--muted)}@media all and (min-device-width: 320px) and (max-device-width: 720px){.section-2{width:100vw}.section-2 .section-wrapper{height:15rem;padding:1rem 1.25rem}}

.section-3{position:relative;background-size:cover;background-position:center;background-repeat:no-repeat;background-image:url(/Travlo/static/media/section-3.47f692d2.png)}.section-3 .section-wrapper{height:100%;padding:1rem;display:flex;flex-direction:column;justify-content:space-between;background:linear-gradient(229.33deg, rgba(14,20,20,0.1) -41.78%, rgba(14,20,20,0.2) 100.54%)}.section-3 .section-heading{display:flex;align-items:center;justify-content:flex-end}.section-3 .section-heading .menu{width:2.5rem;height:2.5rem;cursor:pointer;transition:all .3s ease-in-out}.section-3 .section-heading .menu:hover{transform:scale(1.2)}@media all and (min-device-width: 320px) and (max-device-width: 720px){.section-3{width:100vw}.section-3 .section-wrapper{height:12rem;padding:1rem 1.25rem}.section-3 .section-heading .menu{width:2rem;height:2rem}}

.section-4{position:relative;background-size:cover;background-position:center;background-repeat:no-repeat;background-image:url(/Travlo/static/media/section-4.b4d546e5.png)}.section-4 .section-wrapper{height:100%;padding:1rem;display:flex;flex-direction:column;justify-content:space-between;background:linear-gradient(229.33deg, rgba(14,20,20,0.1) -41.78%, rgba(14,20,20,0.2) 100.54%)}.section-4 .section-heading{display:flex;align-items:center;justify-content:flex-end}.section-4 .section-heading .play-button{top:-2.75rem;left:-2.5rem;width:4.5rem;height:4.5rem;cursor:pointer;position:absolute;transition:all .3s ease-in-out}.section-4 .section-heading .play-button:hover{transform:scale(1.2)}@media all and (min-device-width: 320px) and (max-device-width: 720px){.section-4{width:100vw}.section-4 .section-wrapper{height:10rem;padding:1rem 1.25rem;background:linear-gradient(229.33deg, rgba(14,20,20,0.2) -41.78%, rgba(14,20,20,0.75) 100.54%)}.section-4 .section-heading .play-button{top:0;left:0;right:0;width:2rem;height:2rem;position:relative}}

.section-5{left:0;right:0;bottom:0;z-index:3;height:14vh;position:absolute}.section-5 .section-wrapper{display:grid;grid-template-rows:14vh;justify-content:flex-end;grid-template-columns:repeat(5, 1fr) 16vw}.section-5 .booking-button{display:flex;cursor:pointer;color:var(--white);align-items:center;justify-content:center;background-color:var(--blue);transition:all .3s ease-in-out}.section-5 .booking-button:hover{background-color:#0d4393}.section-5 .booking-button .button-text{font-weight:500;margin-right:.75rem}.section-5 .trial-tab{display:flex;cursor:pointer;padding:0 1.5rem;flex-direction:column;align-items:flex-start;justify-content:center;transition:all .3s ease-in-out}.section-5 .trial-tab:hover{background-color:var(--blue)}.section-5 .trial-tab .trial-icon{height:1.25rem;margin-bottom:.5rem}.section-5 .trial-tab .trial-caption{font-size:.7rem;color:var(--white)}.section-5 .booking-tab{display:grid;grid-gap:0 1.25rem;gap:0 1.25rem;cursor:pointer;padding:0 1.5rem;position:relative;align-items:center;color:var(--text-blue);justify-content:center;background-color:#EBEBEB;grid-template-columns:2rem 1fr;transition:all .3s ease-in-out}.section-5 .booking-tab:hover{color:var(--white) !important;background-color:#0d4393}.section-5 .booking-tab::before{top:0;left:0;height:100%;width:2px;content:'';display:block;position:absolute;background:radial-gradient(4175% 59.22% at 75% 50%, rgba(0,0,0,0.25) 0%, rgba(0,0,0,0) 100%)}.section-5 .booking-tab:nth-child(2)::before{display:none}.section-5 .booking-tab .tab-icon{width:2rem;height:2rem}.section-5 .booking-tab .tab-title{font-size:1rem;grid-column:2/-1;font-weight:700;font-family:var(--bold);text-transform:uppercase;letter-spacing:0.02rem}.section-5 .booking-tab .tab-caption{font-size:.65rem;font-weight:600;grid-column:2/-1;margin-top:0.1rem}.section-5 .booking-tab .tab-footer{font-size:.65rem;font-weight:600;grid-column:2/-1;margin-top:0.25rem}@media all and (min-device-width: 320px) and (max-device-width: 720px){.section-5{z-index:3;height:auto;position:relative}.section-5 .section-wrapper{display:grid;grid-template-columns:1fr;grid-template-rows:17vh repeat(4, 15vh) 12vh}.section-5 .trial-tab{padding:0 1.25rem;background-color:var(--orange)}.section-5 .booking-tab{padding:0 1.25rem}.section-5 .booking-tab::before{top:0;left:0;width:100%;height:1px;background:radial-gradient(4175% 59.22% at 75% 50%, rgba(0,0,0,0.25) 0%, rgba(0,0,0,0) 100%)}}

:root{--text: #66758c;--dark: #222831;--muted: #F7F9FF;--blue: #0F4EAA;--text-blue: #1F3E9A;--dark-blue: #375885;--red: #DB4437;--orange: #FF7043;--border: #D2D2D6;--white: #FFFFFF;--shadow: 0px 1px #E6E9EC;--bold: 'Oswald', -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;--regular: 'Open Sans', -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif}*{box-sizing:border-box}html{font-size:20px;scroll-behavior:smooth}body{margin:0;padding:0;font-size:16px;color:#66758c;color:var(--text);font-weight:400;font-family:'Open Sans', -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;font-family:var(--regular);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}a{color:inherit;text-decoration:none}input,select,textarea,button{padding:0;border:none;outline:none;resize:none;color:#FFFFFF;color:var(--white);font-family:inherit}.noselect{-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.image{background-size:cover;background-position:center;background-repeat:no-repeat;background-color:#D2D2D6;background-color:var(--border)}.App{display:grid;grid-template-rows:100vh;grid-template-columns:1fr}.App .app-body{display:grid;grid-template-rows:46vh 1fr 14vh;grid-template-columns:1fr 35vw 16vw}.App .app-body .section-1{grid-row:1/-1}.App .app-body .section-2{grid-row:1/-2}.App .app-body .section-4{grid-column:3/-1}.App .app-body .section-5{grid-row:3/-1;grid-column:3/-1}.App .app-body .app-section-footer{display:flex;color:#F7F9FF;color:var(--muted);align-items:center;text-transform:uppercase;justify-content:space-between}.App .app-body .app-section-footer.sm{padding-left:1rem;padding-bottom:1rem}.App .app-body .app-section-footer .location-1{font-weight:700;font-size:1.25rem;letter-spacing:0.005rem}.App .app-body .app-section-footer .location-1.sm{font-size:1rem}.App .app-body .app-section-footer .location-2{color:#9E9E9E;font-weight:600}.App .app-body .app-section-footer .location-2.sm{font-size:.6rem;font-weight:700}.App .app-body .app-section-footer .add-location{width:2.6rem;height:2.6rem;cursor:pointer;transition:all .3s ease-in-out}.App .app-body .app-section-footer .add-location:hover{transform:scale(1.2)}.App .app-body .app-section-footer .add-location.sm{width:2rem;height:2rem}@media all and (min-device-width: 320px) and (max-device-width: 720px){.App .app-body{grid-template-columns:1vw;grid-template-rows:repeat(5, auto)}.App .app-body .section-1{grid-row:1;grid-column:1}.App .app-body .section-2{grid-row:2;grid-column:1}.App .app-body .section-3{grid-row:3;grid-column:1}.App .app-body .section-4{grid-row:4;grid-column:1}.App .app-body .section-5{grid-row:5;grid-column:1}.App .app-body .app-section{width:100vw}.App .app-body .app-section-footer.sm{padding:0}.App .app-body .app-section-footer .location-1{font-size:1rem}.App .app-body .app-section-footer .location-1.sm{font-size:1rem}.App .app-body .app-section-footer .location-2{font-size:.7rem;font-weight:600}.App .app-body .app-section-footer .location-2.sm{font-size:.7rem;font-weight:600}.App .app-body .app-section-footer .add-location{width:2.2rem;height:2.2rem}.App .app-body .app-section-footer .add-location.sm{width:2.2rem;height:2.2rem}}

